.song-item {
  display: grid;
  grid-template-columns: auto 50px;
  transition: background-color 400ms ease;
}

.song-item:hover {
  background-color: #b1c2de;
  cursor: pointer;
}

.song-item:hover .button-remove {
  display: block;
}

.button-remove {
  display: none;
  font-size: 8px;
}

.current-song {
  background-color: #556469;
}

.player-container {
  overflow: hidden;
}

#main {
  position: absolute;
  right: -100000px;
}

.main-view {
  display: grid;
  grid-template-columns: auto auto;
  overflow-x: hidden;
  overflow-y: hidden;
}

.library,
.queue {
  overflow-y: auto;
  height: 80vh;
}
